body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-color: #2d3436 !important;
  min-width: 270px;
}

.main{
  display: flex !important;
  flex-direction: row !important;
  justify-content: flex-start !important;
}

.mycss-nbar{
  display: flex;
  background-color: #000;
  color: #fff;
  justify-content: center;
  align-items: center;
  font: outline;
  font-weight: bold;
  font-size: 2.5rem;
  height: 7.5vh;
  position: -webkit-sticky;
  position: sticky;
  top:0;
  overflow: hidden !important;
}

#mycss-area{
  display: flex;
  padding: 1.2rem;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
  background-color: #2d3436;
  min-height: 90vh;
  min-width: 77vw;
}


@media screen and (max-width: 640px){
  .main{
    display: flex !important;
    flex-direction: column-reverse !important;
  }
  .mycss-nbar{
    font-size: 2rem;
    height: 8vh;
  }
}

@media screen and (max-width: 480px) {
  .mycss-nbar{
    font-size: 1.5rem;
    height: 7vh;
  }
}

.sbar-footer-container{
    color: #f0f0f0;
    margin-top: 2rem; 
    margin-bottom: 1rem; 
    padding: 1.3rem;
/*     border: 6px white solid; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.sbar-footer-begin{
    font-size: 1.2rem;
    font-weight: 500;
}

.sbar-footer-list{
    display: flex;
    flex-direction: row;
}

.sbar-footer-list-item{
    margin-top: -15px;
    padding-left: 5px;
    padding-right: 5px;
    font-size: 1rem;
    text-decoration: none;
}

.sbar-footer-end{
/*     color: #0f0f0f; */     
    color: #f3f3f3;
}

a{
    text-decoration: none; 
/*     color: #2D3436 !important; */
}

@media screen and (max-width: 350px){
    .sbar-footer-begin{
        font-size: 1rem;
    }
}
.mycss-sbar{
/*     background-color: #2c3e50 !important; */
    background-color: #132254 !important;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    min-width: 270px;
    overflow: hidden !important;
    padding: 0.6rem;
}

#mycss-sbar-text{  
    color: #fff !important;
    font-size: 1.2rem;
/*     font-weight: bold; */
}

/* @media screen and (min-width:780px){
    .mycss-sbar{
        width: 20vw;
    }
} */

@media screen and (min-width:640px){
    .mycss-sbar{
        width: 22vw;
    }
}
.ubtn{
	padding: 0.25rem 0.5rem;
	border: 3px solid #fefefe;
	border-radius: 7px;	
	color: #fefefe;
	background-color: rgba(0,0,0,0.6);
	font-size: 1.5rem;
	text-transform: capitalize;
	cursor: pointer;
	outline: none;
}
.ubtn:hover{
	background-color: #000;
}

.upload-btn-wrapper {
    position: relative;
    overflow: hidden;
    display: inline-block;
  }
  
/*   .ubtn {
    border: 2px solid gray;
    color: gray;
    background-color: white;
    padding: 8px 20px;
    border-radius: 8px;
    font-size: 20px;
    font-weight: bold;
  }
   */
  .upload-btn-wrapper input[type=file] {
    font-size: 100px;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
  }
.mycss-list{
    color: #f3f3f3;
    font-size: 2.2rem;
}

.mycss-o-img{
    max-height: 90vh;
    max-width: 90vw;
}

@media screen and (max-width: 640px){
    .mycss-list{
        color: #f3f3f3;
        font-size: 1.2rem;
    }   
}
